.layout {
  height: 100vh;
  background-color: var(--purple-light-900);
}

.row {
  display: flex;
  align-items: center;
  padding-top: 35px;
  padding-bottom: 50px;
}

.col {
  &--info {
    max-width: 498px;
    margin-right: 166px;
  }
}

.title {
  width: 500px;
  height: 222px;
  background-color: var(--gray-light-100);
}

.description {
  width: 400px;
  height: 40px;
  margin-top: 20px;
  background-color: var(--gray-light-100);
}

.button {
  width: 384px;
  height: 55px;
  margin-top: 48px;
  background-color: var(--gray-light-100);
}

.image {
  width: 500px;
  height: 400px;
  background-color: var(--gray-light-100);
}
